//
// Plugins / jQuery-minicolors
// --------------------------------------------------


.minicolors { position: relative; }

.minicolors-sprite {
  background-image: url(data:image/png;base64,);
}


// Swatch
//

.minicolors-swatch {
  position: absolute;

  display: inline-block;

  margin: 1px;
  padding: 0;

  cursor: text;
  vertical-align: middle;

  background-position: -80px 0;

  .minicolors input[type=hidden] + & {
    position: static;

    cursor: pointer;
  }

  .minicolors input[type=hidden][disabled] + & { cursor: not-allowed; }
}

.minicolors-swatch-color {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  border-radius: inherit;
}


// Panel
//

.minicolors-panel {
  position: absolute;
  z-index: $zindex-popover;

  display: none;

  box-sizing: content-box;
  width: 173px;
  height: 152px;

  border: $popover-border-width solid;
  border-color: $popover-fallback-border-color;
  border-color: $popover-border-color;
  border-radius: $border-radius-base;
  background: $dropdown-bg;

  &.minicolors-visible { display: block; }

  // Arrow
  &:after {
    position: absolute;

    display: block;

    content: "";

    border-width: $popover-arrow-width;
    border-style: solid;
    border-color: transparent;
  }
}


// Grid
//

.minicolors-grid {
  position: absolute;
  top: 1px;
  left: 1px;

  width: 150px;
  height: 150px;

  cursor: crosshair;

  background-position: -120px 0;
}

.minicolors-grid-inner {
  position: absolute;
  top: 0;
  left: 0;

  width: 150px;
  height: 150px;
}


// Slider
//

.minicolors-slider,
.minicolors-opacity-slider {
  position: absolute;
  top: 1px;
  left: 152px;

  width: 20px;
  height: 150px;

  cursor: row-resize;

  background-color: white;
  background-position: 0 0;
}

.minicolors-opacity-slider {
  left: 173px;

  display: none;

  background-position: -40px 0;
}


// Saturation
//

.minicolors-slider-saturation {
  .minicolors-grid { background-position: -420px 0; }

  .minicolors-grid-inner {
    background-image: inherit;
    background-position: -270px 0;
  }

  .minicolors-slider { background-position: -60px 0; }
}


// Brightness
//

.minicolors-slider-brightness {
  .minicolors-grid { background-position: -570px 0; }
  .minicolors-grid-inner { background-color: black; }
  .minicolors-slider { background-position: -20px 0; }
}

// ---

.minicolors-slider-wheel {
  .minicolors-grid { background-position: -720px 0; }
  .minicolors-slider { background-position: -20px 0; }
}

.minicolors-with-opacity {
  .minicolors-panel { width: 194px; }
  .minicolors-opacity-slider { display: block; }
}


// Pickers
//

.minicolors-grid .minicolors-picker {
  position: absolute;
  top: 70px;
  left: 70px;

  width: 12px;
  height: 12px;
  margin-top: -6px;
  margin-left: -6px;

  border: solid 1px black;
  border-radius: 10px;
  background: none;

  > div {
    position: absolute;
    top: 0;
    left: 0;

    box-sizing: content-box;
    width: 8px;
    height: 8px;

    border: solid 2px white;
    border-radius: 8px;
  }
}

.minicolors-picker {
  position: absolute;
  top: 0;
  left: 0;

  box-sizing: content-box;
  width: 18px;
  height: 2px;
  margin-top: -2px;

  border: solid 1px black;
  background: white;
}


// Panel positioning
//

.minicolors-position-top .minicolors-panel {
  top: -154px;

  margin-top: -$popover-arrow-width - $popover-border-width;

  &:after {
    bottom: -$popover-arrow-width - $popover-border-width;

    border-top-color: $popover-fallback-border-color;
    border-top-color: $popover-border-color;
    border-bottom-width: 0;
  }
}

.minicolors-position-right {
  .minicolors-panel {
    right: 0;

    &:after { margin-right: -$popover-arrow-width - $popover-border-width + 1px; }
  }
}

.minicolors-position-bottom .minicolors-panel {
  top: auto;

  margin-top: $popover-arrow-width;

  &:after {
    top: -$popover-arrow-width - $popover-border-width;

    border-top-width: 0;
    border-bottom-color: $popover-fallback-border-color;
    border-bottom-color: $popover-border-color;
  }
}

.minicolors-position-left {
  .minicolors-panel {
    left: 0;

    &:after { margin-left: -$popover-arrow-width - $popover-border-width + 1px; }
  }
}


// Inline controls
//

.minicolors-inline {
  display: inline-block;

  .minicolors-input { display: none !important; }

  .minicolors-panel {
    position: relative;
    z-index: auto;
    top: auto;
    left: auto;

    display: inline-block;

    border-width: 1px;
    border-radius: 0;
    box-shadow: none;

    &:after { display: none; }
  }
}


// Sizes
//

// Default
.minicolors-input {
  @include minicolors-size-base($padding-base-vertical, $padding-base-horizontal, $border-radius-base, $font-size-base, $line-height-base, 3px);
}
@include minicolors-size(sm, $padding-small-vertical, $padding-small-horizontal, $border-radius-small, $font-size-small, $line-height-small, 2px);
@include minicolors-size(lg, $padding-large-vertical, $padding-large-horizontal, $border-radius-large, $font-size-large, $line-height-large, 5px);
